}

/* === MIGLIORAMENTI RESPONSIVE === */

/* Navigation - Mobile First */
.nav-container {
  padding: 0 1rem;
}

/* Logo e titolo responsive */
.nav-brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  min-width: 0; /* Permette shrinking */
}

.nav-title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #ffffff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Navigation buttons - Responsive */
.nav-buttons {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  flex-shrink: 0;
}

.nav-button {
  padding: 0.375rem 0.75rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.375rem;
  white-space: nowrap;
}

/* Responsive breakpoints */
@media (max-width: 640px) {
  .nav-container {
    padding: 0 0.75rem;
  }
  
  .nav-title {
    font-size: 1rem;
    max-width: 120px;
  }
  
  .nav-button {
    padding: 0.375rem 0.5rem;
    font-size: 0.75rem;
    gap: 0.25rem;
  }
  
  .nav-button .nav-icon {
    width: 14px;
    height: 14px;
  }
  
  .nav-button .nav-text {
    display: none;
  }
}

@media (max-width: 480px) {
  .nav-title {
    display: none;
  }
  
  .nav-button {
    padding: 0.5rem;
  }
  
  .nav-button .nav-text {
    display: none;
  }
}

/* === COMPONENTI MIGLIORATI === */

/* Cards hover effects */
.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  border-color: #6b7280;
}

/* Tables responsive */
@media (max-width: 768px) {
  .table-responsive {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  .table th,
  .table td {
    padding: 0.5rem 0.75rem;
    font-size: 0.8rem;
  }
  
  .table th:first-child,
  .table td:first-child {
    position: sticky;
    left: 0;
    background-color: inherit;
    z-index: 10;
  }
}

/* Modal responsive */
@media (max-width: 640px) {
  .modal-content {
    margin: 1rem;
    max-height: 90vh;
    width: calc(100% - 2rem);
  }
  
  .modal-overlay {
    padding: 1rem;
  }
}

/* Progress bars */
.progress-bar {
  position: relative;
  overflow: hidden;
}

.progress-fill {
  position: relative;
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* === ACCESSIBILITÃ€ === */

/* Focus states migliorati */
.focus-ring:focus,
button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(249, 115, 22, 0.5);
  border-color: #f97316;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .bg-gray-800 { 
    background-color: #000000;
    border: 1px solid #ffffff;
  }
  
  .bg-gray-700 { 
    background-color: #1a1a1a;
  }
  
  .text-gray-300,
  .text-gray-400 {
    color: #ffffff;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  
  .progress-fill::after {
    animation: none;
  }
}

/* === UTILITIES AGGIUNTIVE === */

/* Text truncation */
.text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Scrollbar personalizzata per mobile */
@media (max-width: 768px) {
  ::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
}

/* Loading states */
.loading-skeleton {
  background: linear-gradient(90deg, #374151 25%, #4b5563 50%, #374151 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Notification improvements */
.notification {
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Glassmorphism effect per modal */
.modal-content {
  backdrop-filter: blur(16px);
  background-color: rgba(31, 41, 55, 0.95);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
